.footer-nav {
  display: flex;
  flex-wrap: wrap;
  padding: 30px;
  margin-bottom: 10vh;
}
.footer-nav-item {
  width: 20%;
  padding: 10px;
}
.footer-nav-item img {
  width: 18px;
  vertical-align: middle;
  margin-right: 5px;
}
.footer-nav-item p {
  display: inline;
  vertical-align: middle;
}
.footer-nav a {
  font-size: 17px;
  color: #777;
  transition-duration: 0.2s;
}
.footer-nav a:hover {
  color: rgb(242,151,27);
}
.footer-nav ul {
  margin-top: 10px;
}
.footer-nav ul li {
  padding: 5px 0;
}
.footer-nav ul a {
  font-size: 13px;
}
@media (min-width: 600px) and (max-width:1000px){
  .footer-nav-item {
    width: 25%;
  }
}
@media (max-width:600px) {
  .footer-nav-item {
    width: 100%;
    text-align: center;
  }
  .footer-nav-item ul li {
    padding: 3px 0;
  }
}