.main-right {
  width: 25%;
  padding: 10px 10px;
  font-size: 13px;
  text-align: left;
}
.main-right-time {
  margin-top: 10px;
}
.main-right-time th {
  font-weight: bold;
}
.main-right-time td, .main-right-time th {
  padding: 5px 10px;
}
.main-right div {
  margin-top: 20px;
}
.main-right-holiday h3, .main-right-holiday p {
  padding: 5px 10px;
}
.main-right-address h3 {
  font-weight: bold;
}
.main-right-address h3, .main-right-address p {
  padding: 5px 10px;
}
.main-right-address img {
  margin-top: 20px;
  width: 90%;
}
.main-right-contact h3, .main-right-contact p {
  padding: 5px 10px;
}
.main-right-contact h3 {
  color: rgb(242, 151, 27);
  font-size: 20px;
}
.main-right-line h2 {
  color: orange;
  font-size: 18px;
  margin-bottom: 3px;
}
.main-right-line a {
  color: blue;
  font-size: 18px;
}
.main-right-line-qr {
  display: flex;
}
.main-right-line-qr img {
  width: 30%;
  margin: 0 10px;
}
.main-right-line p {
  margin-top: auto;
  margin-bottom: auto;
  padding: 10px;
}
@media (max-width: 600px) {
  .main-right {
    width: 100%;
    border-bottom: solid 2px rgba(0, 0, 0, 0.1);
  }
}