.main-left {
  width: 75%;
  padding: 5px 30px;
}
section {
  padding: 20px 10px;
  border-bottom: solid 2px rgba(0, 0, 0, 0.1);
  padding-top: 120px;
  margin-top: -120px;
}
section h1, section h2, section h3, section p, section a, section li, section img {
  padding: 10px 0;
}
section h1 {
  font-size: 30px;
  font-weight: bold;
  color:rgb(242, 151, 27);
}
section h2 {
  font-size: 28px;
  font-weight: bold;
}
section p {
  font-size: 16px;
  line-height: 22px;
}
section li, section h3 {
  font-size: 20px;
}
section img {
  max-width: 80%;
}
section a {
  font-size: 20px;
  color: blue;
}
section a:hover {
  opacity: 0.8;
}
@media (max-width: 600px) {
  .main-left {
    width: 100%;
  }
  section h1 {
    font-size: 24px;
  }
  section h2 {
    font-size: 20px;
  }
  section p {
    font-size: 14px;
  }
  section img {
    width: 80%;
  }
  section li, section h3 {
    font-size: 16px;
  }
  section h1, section h2, section h3, section p, section a, section li, section img {
    padding: 3px 0;
  }
}