.header {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
  padding: 10px;
  height: 120px;
}
.header-top img {
  max-width: 40%;
  height: 100%;
  padding-bottom: 20px;
  object-fit: contain;
}
.fas {
  display: none;
}
@media (max-width: 600px) {
  .header {
    height: 80px;
  }
  .header-top img {
    max-width: 60%;
    padding: 0;
  }
  .fas {
    display: inherit;
    position: absolute;
    top: 40px;
    right: 20px;
    transform: translateY(-50%);
    font-size: 25px;
  }
}