.header-nav {
  position: absolute;
  right: 10px;
  z-index: 1;
  max-width: 60%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  transition-duration: 0.3s;
  vertical-align: middle;
}
.nav-item {
  width: 14.2%;
  transition-duration: 0.3s;
  max-height: 100%;
}
.nav-item.current {
  background: rgba(240, 199, 142, 0.7);
}
.nav-item.current:hover {
  background: rgb(240, 199, 142);
  opacity: 0.9;
}
.nav-item:hover {
  background: rgba(0, 0, 0, 0.1);
}
.nav-item a {
  text-decoration: none;
  text-align: center;
}
.nav-item h1 {
  font-size: 15px;
  padding-bottom: 5px;
  padding-top: 3px;
}
.nav-item img {
  width: 45px;
  height: 45px;
  margin-left: auto;
  margin-right: auto;
}
.mask {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 80px;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  transition-duration: 0.3s;
}
@media (max-width: 600px) {
  .header-nav {
    position: fixed;
    max-width: 100%;
    width: 100%;
    padding: 0 20px;
    top: 80px;
    right: 0;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-100%);
    transition-duration: 0.3s;
    flex-direction: column;
    justify-content: space-between;
    height: calc(100vh - 80px - 15vh);
  }
  .nav-item {
    width: 100%;
    padding: 17px 0;
    border-radius: 4px;
  }
  .nav-item img {
    width: 30px;
    height: 30px;
    margin-right: 15px;
    vertical-align: middle;
  }
  .nav-item h1 {
    display: inline;
    vertical-align: middle;
    font-size: 20px;
    letter-spacing: 2px;
    font-weight: bold;
  }
  .header-nav.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
  .mask.active {
    opacity: 1;
    visibility: visible;
  }
}