.footer-images {
  position: fixed;
  left: 0;
  bottom: 200px;
  transform: translateY(200px);
  height: 200px;
  width: 100%;
}
.footer-image {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition-duration: 0.7s;
  opacity: 0;
  visibility: hidden;
  transform: translateY(200px);
}
.footer-image.active {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}
@media (max-width: 600px) {
  .footer-images {
    height: 15vh;
  }
  .footer-images.active {
    transform: translateY(50px);
  }
}