.header-under-nav {
  position: absolute;
  top: 100px;
  width: 100%;
  background-color: #fff;
  display: flex;
}
.header-under-nav p {
  font-weight: bold;
  max-width: 20%;
  padding: 0 10px;
}
.header-under-nav ul {
  display: flex;
  max-width: 80%;
}
.header-under-nav-item {
  padding: 0 10px;
}
.header-under-nav-item.active a {
  color: rgb(242, 151, 27);
}
.header-under-nav-item a {
  font-weight: bold;
  transition-duration: 0.2s;
}
.header-under-nav-item a:hover {
  opacity: 0.8;
}
@media (max-width:600px) {
  .header-under-nav {
    display: none;
  }
}